import React from "react";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from "react-responsive-carousel";

class DemoCarousel extends React.PureComponent {
  render() {
    return (
      <Carousel
        autoPlay={true}
        showThumbs={false}
        interval={3000}
        infiniteLoop
        stopOnHover={false}
      >
        <div style={{ height: "500px" }}>
          <img
            src={require("../assets/carousal1.jpg")}
            style={{ position: "relative" }} alt=""
          />
          <div
            style={{
              position: "absolute",
              left: "35%",
              top: "35%",
              justifyItems: "center"
            }}
          >
            <h1>Dr Minerva Abbott</h1>
            <h2>
              General and Cosmetic Dentistry
              <br />
              San Josa,CA
            </h2>
          </div>
        </div>
        <div style={{ height: "500px" }}>
          <img src={require("../assets/carousal2.png")} alt=""/>
        </div>
        <div style={{ height: "500px" }}>
          <img src={require("../assets/carousal3.jpg")} alt=""/>
        </div>
      </Carousel>
    );
  }
}

export default DemoCarousel;
