import React from "react";

import logo from "../assets/logo1.png";
import AppointmentPopup from "../AppointmentPopup/index";
import BookingPopup from "../BookingPopup/index";

require("./styles.css");


class TopNav extends React.PureComponent {
  render() {
    const topNavTabs = this.props.topNavTabs;

    return (
      <div class="ui eight item menu" style={{ margin: 0 }}>
        <img class="item" src={logo} alt="" />
        {topNavTabs.map(item => (
          <a class="item" href=" ">
            {item}
          </a>
        ))}
        <BookingPopup />
      </div>
    );
  }
}

export default TopNav;
